import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class OrgDevices extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            orgDeviceList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Device Id',
                },
                {
                    field: 'deviceType',
                    headerName: 'Device Type',
                },
                {
                    field: 'model',
                    headerName: 'Device Model',
                },
                {
                    field: 'deviceStatus',
                    headerName: 'Device Status',
                    valueGetter: function (param) {
                        return param.data.deviceStatus == 0 ? 'InActive' : 'Active'
                    },
                    cellStyle: function (param) {
                        if (param.data.deviceStatus == 1) {
                            return { color: 'green' };
                        } else {
                            return { color: 'red' };
                        }
                    }
                },
                {
                    field: 'deviceDescription',
                    headerName: 'Description',
                },
                {
                    field: 'deviceModel',
                    headerName: 'Model',
                },
                {
                    field: 'deviceCode',
                    headerName: 'Code',
                },
                {
                    field: 'deviceLocation',
                    headerName: 'Location',
                },
                {
                    field: 'deviceIP',
                    headerName: 'IP',
                },
                {
                    field: 'deviceCredentials',
                    headerName: 'Credentials',
                },
                {
                    field: 'deviceConnectionType',
                    headerName: 'Connection Type',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorOrgDevice: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            deviceType: '',
            deviceId: null,
            deviceDescription: '',
            deviceModel: '',
            deviceCode: '',
            deviceLocation: '',
            deviceIP: '',
            deviceCredentials: '',
            deviceConnectionType: '',
            deviceStatus: '',
            selectedCountry: null,
            countryList: [],

            deviceList: []

        }
        this.onCreateOrgDevice = this.onCreateOrgDevice.bind(this)
        this.onAddEditOrgDevice = this.onAddEditOrgDevice.bind(this)
        this.onDeleteOrgDevice = this.onDeleteOrgDevice.bind(this)
        this.onEditOrgDevice = this.onEditOrgDevice.bind(this)
        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadOrgDevices()
        this.loadDevices()

    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadOrgDevices() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'orgDevices')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadDevices() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'devices')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    deviceList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateOrgDevice() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            deviceType,
            deviceId,
            deviceDescription,
            deviceModel,
            deviceCode,
            deviceLocation,
            deviceIP,
            deviceCredentials,
            deviceConnectionType,
            deviceStatus,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'orgDevices')
            .send({
                deviceType,
                deviceId,
                deviceDescription,
                deviceModel,
                deviceCode,
                deviceLocation,
                deviceIP,
                deviceCredentials,
                deviceConnectionType,
                deviceStatus,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                deviceType: '',
                deviceId: null,
                deviceDescription: '',
                deviceModel: '',
                deviceCode: '',
                deviceLocation: '',
                deviceIP: '',
                deviceCredentials: '',
                deviceConnectionType: '',
                deviceStatus: '',
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadOrgDevices()
                confirmAlert({
                    title: 'OrgDevices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'OrgDevices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditOrgDevice() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            deviceType,
            deviceId,
            deviceDescription,
            deviceModel,
            deviceCode,
            deviceLocation,
            deviceIP,
            deviceCredentials,
            deviceConnectionType,
            deviceStatus,
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'orgDevices/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                deviceType,
                deviceId,
                deviceDescription,
                deviceModel,
                deviceCode,
                deviceLocation,
                deviceIP,
                deviceCredentials,
                deviceConnectionType,
                deviceStatus,
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadOrgDevices()
                confirmAlert({
                    title: 'OrgDevices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'OrgDevices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearOrgDevice = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditOrgDevice(type) {
        const { selectedRow, deviceList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update OrgDevice',
                    id: selectedRow.id,
                    deviceType: selectedRow.deviceType,
                    deviceId: selectedRow.deviceId,
                    deviceDescription: selectedRow.deviceDescription,
                    deviceModel: selectedRow.deviceModel,
                    deviceCode: selectedRow.deviceCode,
                    deviceLocation: selectedRow.deviceLocation,
                    deviceIP: selectedRow.deviceIP,
                    deviceCredentials: selectedRow.deviceCredentials,
                    deviceConnectionType: selectedRow.deviceConnectionType,
                    deviceStatus: selectedRow.deviceStatus,
                    selectedDeviceId: { id: selectedRow.deviceId, deviceModel: deviceList.find(x => x.id === selectedRow.deviceId).deviceModel }
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add OrgDevice',
                    id: null,
                    deviceType: '',
                    deviceId: null,
                    deviceDescription: '',
                    deviceModel: '',
                    deviceCode: '',
                    deviceLocation: '',
                    deviceIP: '',
                    deviceCredentials: '',
                    deviceConnectionType: '',
                    deviceStatus: '',
                })
            }
        }
    }

    async onDeleteOrgDevice() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete OrgDevice',
            message: "Are you sure you wants to delete the record : " + selectedRow.deviceCode + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteOrgDevice()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteOrgDevice() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'orgdevices/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                deviceType: '',
                deviceId: null,
                deviceDescription: '',
                deviceModel: '',
                deviceCode: '',
                deviceLocation: '',
                deviceIP: '',
                deviceCredentials: '',
                deviceConnectionType: '',
                deviceStatus: '',
                selectedRow: null
            }, () => {
                this.loadOrgDevices()
                confirmAlert({
                    title: 'OrgDevice',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'OrgDevice',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorOrgDeviceChange = (editorOrgDevice) => {
        this.setState({
            editorOrgDevice,
            description: editorOrgDevice && draftToMarkdown(convertToRaw(editorOrgDevice.getCurrentContent())),
        })
    }

    onChangeDevice = (newValue, actionMeta) => {

        if (actionMeta.action == "select-option") {
            this.setState({
                selectedDeviceId: newValue,
                deviceId: newValue.id
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    onCheckboxChange = (e) => {
        this.setState({
            [e.target.id]: e.target.checked ? 1 : 0
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditOrgDevice(type)
        })
    }


    render() {
        const { orgDeviceList, deviceList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteOrgDevice}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditOrgDevice.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditOrgDevice.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearOrgDevice()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceType">Device Type </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceType" value={this.state.deviceType} className="form-control" />
                                        {this.validator.message('deviceType', this.state.deviceType, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceId">Device Id </label>
                                        <Select
                                            id={"deviceId"}
                                            value={this.state.selectedDeviceId}
                                            onChange={this.onChangeDevice}
                                            options={deviceList}
                                            getOptionLabel={(option) => option.deviceModel}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('deviceId', this.state.deviceId, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceModel">Device Model </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceModel" value={this.state.deviceModel} className="form-control" />
                                        {this.validator.message('deviceModel', this.state.deviceModel, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceCode">Device Code </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceCode" value={this.state.deviceCode} className="form-control" />
                                        {this.validator.message('deviceCode', this.state.deviceCode, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceLocation">Device Location </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceLocation" value={this.state.deviceLocation} className="form-control" />
                                        {this.validator.message('deviceLocation', this.state.deviceLocation, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceIP">Device IP </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceIP" value={this.state.deviceIP} className="form-control" />
                                        {this.validator.message('deviceIP', this.state.deviceIP, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceCredentials">Device Credentials </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceCredentials" value={this.state.deviceCredentials} className="form-control" />
                                        {this.validator.message('deviceCredentials', this.state.deviceCredentials, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceConnectionType">Connection Type </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceConnectionType" value={this.state.deviceConnectionType} className="form-control" />
                                        {this.validator.message('deviceConnectionType', this.state.deviceConnectionType, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceDescription">Device Description </label>
                                        <textarea onChange={this.onTextChange} id="deviceDescription" value={this.state.deviceDescription} className="form-control" />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceStatus">Device Status </label>
                                        <div className="custom-control custom-switch">
                                            <input type="checkbox" className="custom-control-input"
                                                onChange={this.onCheckboxChange} id="deviceStatus"
                                                value={this.state.deviceStatus}
                                                checked={this.state.deviceStatus == 1 ? true : false}
                                            />
                                            <label className="custom-control-label" for="deviceStatus"></label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearOrgDevice()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update OrgDevice' ? this.onEditOrgDevice : this.onCreateOrgDevice}
                            >{'Submit'}</button>
                        </div>
                    </section>
                </div >
            </LoadingOverlay >
        )
    }
}

const mapOrgDeviceToProps = orgDevice => ({
    tabSettings: orgDevice.userReducer.tabSettings

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapOrgDeviceToProps,
    mapDispatchToProps
)(OrgDevices)
