import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class Organization extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            roleList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Organization Id',
                },
                {
                    field: 'orgName',
                    headerName: 'Name',
                },
                {
                    field: 'orgCode',
                    headerName: 'Organization Code',
                },
                {
                    field: 'orgEmail',
                    headerName: 'Email',
                },
                {
                    field: 'orgStatus',
                    headerName: 'Status',
                    valueGetter: function (param) {
                        return param.data.orgStatus == 0 ? 'InActive' : 'Active'
                    },
                    cellStyle: function (param) {
                        if (param.data.orgStatus == 1) {
                            return { color: 'green' };
                        } else {
                            return { color: 'red' };
                        }
                    }
                },
                {
                    field: 'orgType',
                    headerName: 'Organization Type',
                },
                {
                    field: 'entityExport',
                    headerName: 'Entity Export',
                },
                {
                    field: 'orgDescription',
                    headerName: 'Description',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorOrganization: null,
            userOrganizationList: [],
            selectedUserOrganization: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,
            orgName: '',
            orgCode: '',
            orgDescription: '',
            orgStatus: null,
            orgType: '',
            entityExport: '',
            orgEmail: ''

        }
        this.onCreateOrganization = this.onCreateOrganization.bind(this)
        this.onAddEditOrganization = this.onAddEditOrganization.bind(this)
        this.onDeleteOrganization = this.onDeleteOrganization.bind(this)
        this.onEditOrganization = this.onEditOrganization.bind(this)
        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadOrganizations()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadOrganizations() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'organizations')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateOrganization() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            orgName,
            orgCode,
            orgDescription,
            orgStatus,
            orgType,
            entityExport,
            orgEmail
        } = this.state

        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'organizations')
            .send({
                orgName,
                orgCode,
                orgDescription,
                orgStatus,
                orgType,
                entityExport,
                orgEmail
            })
        const response = r.body

        if (response.success) {
            this.setState({
                orgName: '',
                orgCode: '',
                orgDescription: '',
                orgStatus: null,
                orgType: '',
                entityExport: '',
                orgEmail: '',
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadOrganizations()
                confirmAlert({
                    title: 'Organizations',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Organizations',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditOrganization() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            orgName,
            orgCode,
            orgDescription,
            orgStatus,
            orgType,
            entityExport,
            orgEmail
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'organizations/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                orgName,
                orgCode,
                orgDescription,
                orgStatus,
                orgType,
                entityExport,
                orgEmail
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadOrganizations()
                confirmAlert({
                    title: 'Organizations',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Organizations',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearOrganization = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditOrganization(type) {
        const { selectedRow } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update Organization',
                    id: selectedRow.id,
                    orgName: selectedRow.orgName,
                    orgCode: selectedRow.orgCode,
                    orgDescription: selectedRow.orgDescription,
                    orgStatus: selectedRow.orgStatus,
                    orgType: selectedRow.orgType,
                    entityExport: selectedRow.entityExport,
                    orgEmai: selectedRow.orgEmail
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add Organization',
                    id: null,
                    orgName: '',
                    orgCode: '',
                    orgDescription: '',
                    orgStatus: null,
                    orgType: '',
                    entityExport: '',
                    orgEmail: ''
                })
            }
        }
    }

    async onDeleteOrganization() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete Organization',
            message: "Are you sure you wants to delete the record : " + selectedRow.orgName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteOrganization()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteOrganization() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'organizations/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                orgName: '',
                orgCode: '',
                orgDescription: '',
                orgStatus: null,
                orgType: '',
                entityExport: '',
                orgEmail: '',
                selectedRow: null
            }, () => {
                this.loadOrganizations()
                confirmAlert({
                    title: 'Organization',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Organization',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorOrganizationChange = (editorOrganization) => {
        this.setState({
            editorOrganization,
            description: editorOrganization && draftToMarkdown(convertToRaw(editorOrganization.getCurrentContent())),
        })
    }

    onCountryChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedCountry: newValue,
            })
        }
    }


    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditOrganization(type)
        })
    }

    onCheckboxChange = (e) => {
        this.setState({
            [e.target.id]: e.target.checked ? 1 : 0
        })
    }

    render() {
        const { roleList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteOrganization}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditOrganization.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditOrganization.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearOrganization()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="OrganizationNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgName">Organization Name </label>
                                        <input type="text" onChange={this.onTextChange} id="orgName" value={this.state.orgName} className="form-control" />
                                        {this.validator.message('typeDefault', this.state.typeDefault, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgStatus">Organization Status </label>
                                        <div className="custom-control custom-switch">
                                            <input type="checkbox" className="custom-control-input"
                                                onChange={this.onCheckboxChange} id="orgStatus"
                                                value={this.state.orgStatus}
                                                checked={this.state.orgStatus == 1 ? true : false}
                                            />
                                            <label className="custom-control-label" for="orgStatus"></label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgCode">Organization Code </label>
                                        <input type="text" onChange={this.onTextChange} id="orgCode" value={this.state.orgCode} className="form-control" />
                                        {this.validator.message('orgCode', this.state.orgCode, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgType">Organization Type </label>
                                        <input type="text" onChange={this.onTextChange} id="orgType" value={this.state.orgType} className="form-control" />
                                        {this.validator.message('orgType', this.state.orgType, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="entityExport">Entity Export </label>
                                        <input type="text" onChange={this.onTextChange} id="entityExport" value={this.state.entityExport} className="form-control" />
                                        {this.validator.message('entityExport', this.state.entityExport, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgEmail">Organization Email </label>
                                        <input type="text" onChange={this.onTextChange} id="orgEmail" value={this.state.orgEmail} className="form-control" />
                                        {this.validator.message('orgEmail', this.state.orgEmail, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group">
                                <label style={{ fontWeight: 200 }} htmlFor="orgDescription">Organization Description </label>
                                <textarea onChange={this.onTextChange} id="orgDescription" value={this.state.orgDescription} className="form-control" />
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearOrganization()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update Organization' ? this.onEditOrganization : this.onCreateOrganization}
                            >{'Submit'}</button>
                        </div>
                    </section >
                </div >
            </LoadingOverlay >
        )
    }
}

const mapOrganizationToProps = role => ({
    tabSettings: role.userReducer.tabSettings,
    roleList: role.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapOrganizationToProps,
    mapDispatchToProps
)(Organization)
