import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'

class Dashboard extends Component {

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    render() {
        const { tabNames } = this.props
        return (
            <div className="card" style={{ paddingTop: '20px' }}>
                <section className="container-fluid ">
                    <div className="row">
                        {
                            tabNames.map((val, indx) => {
                                return (
                                    <div className="col-lg-2 col-6">
                                        <div className="small-box bg-info">
                                            <div className="inner">
                                                <h3 style={{ color: 'transparent' }}>-</h3>
                                                <h5>{tabNames[indx]}</h5>
                                            </div>
                                            <div className="icon">
                                                <i className="ion ion-bag"></i>
                                            </div>
                                            <a onClick={this.onClickTab.bind(this, tabNames[indx])} className="small-box-footer">More info <i className="fas fa-arrow-circle-right"></i></a>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </section>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    tabSettings: state.userReducer.tabSettings,
    tabNames: state.userReducer.tabNames,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Dashboard)
