import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class JobMaster extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            vendorList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Job Master Id',
                },
                {
                    field: 'jobName',
                    headerName: 'Job Name',
                },
                {
                    field: 'jobSchedule',
                    headerName: 'Job Schedule',
                },
                {
                    field: 'jobType',
                    headerName: 'Job Type',
                },
                {
                    field: 'jobConfig',
                    headerName: 'Job Config',
                },
                {
                    field: 'orgName',
                    headerName: 'Organization',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorJobMaster: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            jobName: '',
            jobSchedule: '',
            jobType: '',
            jobConfig: '',
            orgId: null,
            selectedOrg: null,
            orgList: []

        }
        this.onCreateJobMaster = this.onCreateJobMaster.bind(this)
        this.onAddEditJobMaster = this.onAddEditJobMaster.bind(this)
        this.onDeleteJobMaster = this.onDeleteJobMaster.bind(this)
        this.onEditJobMaster = this.onEditJobMaster.bind(this)
        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadJobMaster()
        this.loadOrganization()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadJobMaster() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'jobmaster')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadOrganization() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'organizations')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    orgList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }


    async onCreateJobMaster() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            jobName,
            jobSchedule,
            jobType,
            jobConfig,
            orgId,


        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'jobmaster')
            .send({
                jobName,
                jobSchedule,
                jobType,
                jobConfig,
                orgId,

            })
        const response = r.body

        if (response.success) {
            this.setState({
                jobName: '',
                jobSchedule: '',
                jobType: '',
                jobConfig: '',
                orgId: null,
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadJobMaster()
                confirmAlert({
                    title: 'JobMaster',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'JobMaster',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditJobMaster() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            jobName,
            jobSchedule,
            jobType,
            jobConfig,
            orgId,
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'jobmaster/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                jobName,
                jobSchedule,
                jobType,
                jobConfig,
                orgId,
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadJobMaster()
                confirmAlert({
                    title: 'JobMaster',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'JobMaster',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearJobMaster = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditJobMaster(type) {
        const { selectedRow, orgList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update JobMaster',
                    id: selectedRow.id,
                    jobName: selectedRow.jobName,
                    jobSchedule: selectedRow.jobSchedule,
                    jobType: selectedRow.jobType,
                    jobConfig: selectedRow.jobConfig,
                    orgId: selectedRow.orgId,
                    selectedOrg: { id: selectedRow.orgId, orgName: orgList.find(x => x.id === selectedRow.orgId).orgName }
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add JobMaster',
                    id: null,
                    jobName: '',
                    jobSchedule: '',
                    jobType: '',
                    jobConfig: '',
                    orgId: null,
                })
            }
        }
    }

    async onDeleteJobMaster() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete JobMaster',
            message: "Are you sure you wants to delete the record : " + selectedRow.jobName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteJobMaster()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteJobMaster() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'jobmaster/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                jobName: '',
                jobSchedule: '',
                jobType: '',
                jobConfig: '',
                orgId: null,
                selectedRow: null
            }, () => {
                this.loadJobMaster()
                confirmAlert({
                    title: 'JobMaster',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'JobMaster',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorJobMasterChange = (editorJobMaster) => {
        this.setState({
            editorJobMaster,
            description: editorJobMaster && draftToMarkdown(convertToRaw(editorJobMaster.getCurrentContent())),
        })
    }

    onChangeOrg = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedOrg: newValue,
                orgId: newValue.id
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditJobMaster(type)
        })
    }

    render() {
        const { vendorList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteJobMaster}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditJobMaster.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditJobMaster.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearJobMaster()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}

                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobName">Job Name </label>
                                        <input type="text" onChange={this.onTextChange} id="jobName" value={this.state.jobName} className="form-control" />
                                        {this.validator.message('jobName', this.state.jobName, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobSchedule">Job Schedule </label>
                                        <input type="text" onChange={this.onTextChange} id="jobSchedule" value={this.state.jobSchedule} className="form-control" />
                                        {this.validator.message('jobSchedule', this.state.jobSchedule, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobType">Job Type </label>
                                        <input type="text" onChange={this.onTextChange} id="jobType" value={this.state.jobType} className="form-control" />
                                        {this.validator.message('jobType', this.state.jobType, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobConfig">Job Config </label>
                                        <input type="text" onChange={this.onTextChange} id="jobConfig" value={this.state.jobConfig} className="form-control" />
                                        {this.validator.message('jobConfig', this.state.jobConfig, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgId">Organization </label>
                                        <Select
                                            id={"org"}
                                            value={this.state.selectedOrg}
                                            onChange={this.onChangeOrg}
                                            options={this.state.orgList}
                                            getOptionLabel={(option) => option.orgName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedOrg', this.state.selectedOrg, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearJobMaster()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update JobMaster' ? this.onEditJobMaster : this.onCreateJobMaster}
                            >{'Submit'}</button>
                        </div>
                    </section>
                </div>
            </LoadingOverlay>
        )
    }
}

const mapJobMasterToProps = vendor => ({
    tabSettings: vendor.userReducer.tabSettings,
    vendorList: vendor.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapJobMasterToProps,
    mapDispatchToProps
)(JobMaster)
