import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class Vendors extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            vendorList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Vendor Id',
                },
                {
                    field: 'vendorCode',
                    headerName: 'Vendor Code',
                },
                {
                    field: 'vendorName',
                    headerName: 'Vendor Name',
                },
                {
                    field: 'vendorStatus',
                    headerName: 'Vendor Status',
                    valueGetter: function (param) {
                        return param.data.vendorStatus == 0 ? 'InActive' : 'Active'
                    },
                    cellStyle: function (param) {
                        if (param.data.vendorStatus == 1) {
                            return { color: 'green' };
                        } else {
                            return { color: 'red' };
                        }
                    }
                },
                {
                    field: 'vendorWeb',
                    headerName: 'Vendor Web',
                },
                {
                    field: 'vendorDescription',
                    headerName: 'Vendor Description',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorVendor: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            vendorName: "",
            vendorStatus: "",
            countryId: null,
            selectedCountry: null,
            countryList: []

        }
        this.onCreateVendor = this.onCreateVendor.bind(this)
        this.onAddEditVendor = this.onAddEditVendor.bind(this)
        this.onDeleteVendor = this.onDeleteVendor.bind(this)
        this.onEditVendor = this.onEditVendor.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadVendors()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadVendors() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'vendors')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    vendorList: response.ResponsePacket,
                    rowData: response.ResponsePacket,
                    isLoading: false
                }, () => {
                    console.log(this.state.vendorList)
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateVendor() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            vendorCode,
            vendorName,
            vendorDescription,
            vendorWeb,
            vendorStatus

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'vendors')
            .send({
                vendorCode,
                vendorName,
                vendorDescription,
                vendorWeb,
                vendorStatus
            })
        const response = r.body

        if (response.success) {
            this.setState({
                vendorCode: '',
                vendorName: '',
                vendorDescription: '',
                vendorWeb: '',
                vendorStatus: '',
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadVendors()
                confirmAlert({
                    title: 'Vendors',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Vendors',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditVendor() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            vendorCode,
            vendorName,
            vendorDescription,
            vendorWeb,
            vendorStatus
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'vendors/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                vendorCode,
                vendorName,
                vendorDescription,
                vendorWeb,
                vendorStatus
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadVendors()
                confirmAlert({
                    title: 'Vendors',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Vendors',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearVendor = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditVendor(type) {
        const { selectedRow } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update Vendor',
                    id: selectedRow.id,
                    vendorCode: selectedRow.vendorCode,
                    vendorName: selectedRow.vendorName,
                    vendorDescription: selectedRow.vendorDescription,
                    vendorWeb: selectedRow.vendorDescription,
                    vendorStatus: selectedRow.vendorDescription
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add Vendor',
                    id: null,
                    vendorCode: '',
                    vendorName: '',
                    vendorDescription: '',
                    vendorWeb: '',
                    vendorStatus: ''
                })
            }
        }
    }

    async onDeleteVendor() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete Vendor',
            message: "Are you sure you wants to delete the record : " + selectedRow.vendorName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteVendor()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteVendor() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'vendors/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                vendorName: '',
                vendorStatus: '',
                countryId: null,
                selectedRow: null
            }, () => {
                this.loadVendors()
                confirmAlert({
                    title: 'Vendor',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Vendor',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    onCheckboxChange = (e) => {
        this.setState({
            [e.target.id]: e.target.checked ? 1 : 0
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorVendorChange = (editorVendor) => {
        this.setState({
            editorVendor,
            description: editorVendor && draftToMarkdown(convertToRaw(editorVendor.getCurrentContent())),
        })
    }

    onCountryChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedCountry: newValue,
            })
        }
    }


    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditVendor(type)
        })
    }

    render() {
        const { vendorList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteVendor}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditVendor.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditVendor.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearVendor()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="vendorCode">Vendor Code </label>
                                        <input type="text" onChange={this.onTextChange} id="vendorCode" value={this.state.vendorCode} className="form-control" />
                                        {this.validator.message('vendorCode', this.state.vendorCode, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="vendorName">Vendor Name </label>
                                        <input type="text" onChange={this.onTextChange} id="vendorName" value={this.state.vendorName} className="form-control" />
                                        {this.validator.message('vendorName', this.state.vendorName, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="vendorWeb">Vendor Web </label>
                                        <input type="text" onChange={this.onTextChange} id="vendorWeb" value={this.state.vendorWeb} className="form-control" />
                                        {this.validator.message('vendorWeb', this.state.vendorWeb, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        {/* <input type="text" onChange={this.onTextChange} id="vendorStatus" value={this.state.vendorStatus} className="form-control" /> */}
                                        <div className="form-group">
                                            <label style={{ fontWeight: 200 }} htmlFor="vendorStatus">Vendor Status </label>
                                            <div className="custom-control custom-switch">
                                                <input type="checkbox" className="custom-control-input"
                                                    onChange={this.onCheckboxChange} id="vendorStatus"
                                                    value={this.state.vendorStatus}
                                                    checked={this.state.vendorStatus == 1 ? true : false}
                                                />
                                                <label className="custom-control-label" for="vendorStatus"></label>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="form-group">
                                <label style={{ fontWeight: 200 }} htmlFor="vendorDescription">Vendor Description </label>
                                <textarea onChange={this.onTextChange} id="vendorDescription" value={this.state.vendorDescription} className="form-control" />
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearVendor()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update Vendor' ? this.onEditVendor : this.onCreateVendor}
                            >{'Submit'}</button>
                        </div>
                    </section>
                </div>
            </LoadingOverlay>
        )
    }
}

const mapVendorToProps = vendor => ({
    tabSettings: vendor.userReducer.tabSettings,
    vendorList: vendor.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapVendorToProps,
    mapDispatchToProps
)(Vendors)
