import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class DeviceLog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Log Id',
                },
                {
                    field: 'logDateTime',
                    headerName: 'Log DateTime',
                },
                {
                    field: 'logMessage',
                    headerName: 'Log Message',
                },
                {
                    field: 'logMessageType',
                    headerName: 'Log Message Type',
                },
                {
                    field: 'logType',
                    headerName: 'log Type',
                },
                {
                    field: 'orgDeviceId',
                    headerName: 'Org DeviceId',
                },
                {
                    field: 'orgName',
                    headerName: 'Org Id',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorDeviceLog: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            logDateTime: '',
            logMessage: '',
            logMessageType: '',
            logType: '',
            orgDeviceId: '',
            orgId: null,
            selectedOrg: null,

            orgList: [],
            orgDeviceList: []
        }
        this.onCreateDeviceLog = this.onCreateDeviceLog.bind(this)
        this.onAddEditDeviceLog = this.onAddEditDeviceLog.bind(this)
        this.onDeleteDeviceLog = this.onDeleteDeviceLog.bind(this)
        this.onEditDeviceLog = this.onEditDeviceLog.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadDeviceLog()
        this.loadOrganization()
        this.loadOrgDevices()

    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadDeviceLog() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'devicelogs')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadOrganization() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'organizations')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    orgList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadOrgDevices() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'orgdevices')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    orgDeviceList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateDeviceLog() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            logDateTime,
            logMessage,
            logMessageType,
            logType,
            orgDeviceId,
            orgId,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'devicelogs')
            .send({
                logDateTime,
                logMessage,
                logMessageType,
                logType,
                orgDeviceId,
                orgId,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                logDateTime: '',
                logMessage: '',
                logMessageType: '',
                logType: '',
                orgDeviceId: '',
                orgId: null,
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadDeviceLog()
                confirmAlert({
                    title: 'DeviceLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'DeviceLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditDeviceLog() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            logDateTime,
            logMessage,
            logMessageType,
            logType,
            orgDeviceId,
            orgId,
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'devicelogs/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                logDateTime,
                logMessage,
                logMessageType,
                logType,
                orgDeviceId,
                orgId,
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadDeviceLog()
                confirmAlert({
                    title: 'DeviceLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'DeviceLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearDeviceLog = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditDeviceLog(type) {
        const { selectedRow, orgList, orgDeviceList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {

                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update DeviceLog',
                    id: selectedRow.id,
                    logDateTime: selectedRow.logDateTime,
                    logMessage: selectedRow.logMessage,
                    logMessageType: selectedRow.logMessageType,
                    logType: selectedRow.logType,
                    orgDeviceId: selectedRow.orgDeviceId,
                    orgId: selectedRow.orgId,

                    selectedOrg: { id: selectedRow.orgId, orgName: orgList.find(x => x.id === selectedRow.orgId).orgName },
                    selectedOrgDevice: { id: selectedRow.orgDeviceId, deviceCode: orgDeviceList.find(x => x.id === selectedRow.orgDeviceId).deviceCode }
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add DeviceLog',
                    id: null,
                    logDateTime: '',
                    logMessage: '',
                    logMessageType: '',
                    logType: '',
                    orgDeviceId: '',
                    orgId: null,
                })
            }
        }
    }

    async onDeleteDeviceLog() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete DeviceLog',
            message: "Are you sure you wants to delete the record : " + selectedRow.logDateTime + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteDeviceLog()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteDeviceLog() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'devicelogs/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                logDateTime: '',
                logMessage: '',
                logMessageType: '',
                logType: '',
                orgDeviceId: '',
                orgId: null,
                selectedRow: null
            }, () => {
                this.loadDeviceLog()
                confirmAlert({
                    title: 'DeviceLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'DeviceLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    onEditorDeviceLogChange = (editorDeviceLog) => {
        this.setState({
            editorDeviceLog,
            description: editorDeviceLog && draftToMarkdown(convertToRaw(editorDeviceLog.getCurrentContent())),
        })
    }

    onChangeOrg = (newValue, actionMeta) => {

        if (actionMeta.action == "select-option") {
            this.setState({
                selectedOrg: newValue,
                orgId: newValue.id
            })
        }
    }

    onChangeOrgDevice = (newValue, actionMeta) => {

        if (actionMeta.action == "select-option") {
            this.setState({
                selectedOrgDevice: newValue,
                orgDeviceId: newValue.deviceId
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditDeviceLog(type)
        })
    }

    render() {
        const { isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteDeviceLog}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditDeviceLog.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditDeviceLog.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearDeviceLog()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="logDateTime">Log DateTime </label>
                                        <input type="text" onChange={this.onTextChange} id="logDateTime" value={this.state.logDateTime} className="form-control" />
                                        {this.validator.message('logDateTime', this.state.logDateTime, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="logMessageType">Log MessageType </label>
                                        <input type="text" onChange={this.onTextChange} id="logMessageType" value={this.state.logMessageType} className="form-control" />
                                        {this.validator.message('logMessageType', this.state.logMessageType, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="logType">Log Type </label>
                                        <input type="text" onChange={this.onTextChange} id="logType" value={this.state.logType} className="form-control" />
                                        {this.validator.message('logType', this.state.logType, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgDeviceId">Org Device Id </label>
                                        <Select
                                            id={"orgDevice"}
                                            value={this.state.selectedOrgDevice}
                                            onChange={this.onChangeOrgDevice}
                                            options={this.state.orgDeviceList}
                                            getOptionLabel={(option) => option.deviceCode}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('orgDeviceId', this.state.orgDeviceId, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="logMessage">Log Message </label>
                                        <textarea onChange={this.onTextChange} id="logMessage" value={this.state.logMessage} className="form-control" />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgId">Organization </label>
                                        <Select
                                            id={"org"}
                                            value={this.state.selectedOrg}
                                            onChange={this.onChangeOrg}
                                            options={this.state.orgList}
                                            getOptionLabel={(option) => option.orgName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedOrg', this.state.selectedOrg, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearDeviceLog()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update DeviceLog' ? this.onEditDeviceLog : this.onCreateDeviceLog}
                            >{'Submit'}</button>
                        </div>
                    </section >
                </div >
            </LoadingOverlay >
        )
    }
}

const mapDeviceLogToProps = vendor => ({
    tabSettings: vendor.userReducer.tabSettings
})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapDeviceLogToProps,
    mapDispatchToProps
)(DeviceLog)
