import React, { Component } from 'react';
import { connect } from 'react-redux'

import Home from '../adminPanel/Home';
import Dashboard from '../adminPanel/Dashboard'
import Settings from '../adminPanel/Settings'
import Users from '../adminPanel/Users';
import Countries from '../adminPanel/Countries';
import States from '../adminPanel/States';
import Vendor from '../adminPanel/Vendor';
import Device from '../adminPanel/Device';
import Roles from '../adminPanel/Roles';
import RolesEntity from '../adminPanel/RolesEntity';
import Organization from '../adminPanel/Organization';

import CategoriesType from '../adminPanel/CategoriesType';
import Config from '../adminPanel/Config';
import DeviceLog from '../adminPanel/DeviceLog';
import JobLog from '../adminPanel/JobLog';
import JobMaster from '../adminPanel/JobMaster';
import OrgDevices from '../adminPanel/OrgDevices';
import Reports from '../adminPanel/Reports';
import RoleDetails from '../adminPanel/RoleDetails';
import UserRolesMapping from '../adminPanel/UserRolesMapping';

import Header from './Header';
import SideBar from './SideBar';
import Footer from './Footer';

class AdminContent extends Component {
    constructor(props) {
        super(props)
    }

    renderPage = () => {
        const { tabSettings, tabNames } = this.props
        if (tabSettings.TabName == tabNames[0]) {
            return <Home />      
        } else if (tabSettings.TabName == tabNames[1]) {
            return <Users />
        } else if (tabSettings.TabName == tabNames[2]) {
            return <Countries />
        } else if (tabSettings.TabName == tabNames[3]) {
            return <States />
        } else if (tabSettings.TabName == tabNames[4]) {
            return <Vendor />
        } else if (tabSettings.TabName == tabNames[5]) {
            return <Device />
        } else if (tabSettings.TabName == tabNames[6]) {
            return <Roles />
        } else if (tabSettings.TabName == tabNames[7]) {
            return <RolesEntity />
        } else if (tabSettings.TabName == tabNames[8]) {
            return <Organization />
        } else if (tabSettings.TabName == tabNames[9]) {
            return <CategoriesType />
        } else if (tabSettings.TabName == tabNames[10]) {
            return <Config />
        } else if (tabSettings.TabName == tabNames[11]) {
            return <DeviceLog />
        } else if (tabSettings.TabName == tabNames[12]) {
            return <JobLog />
        } else if (tabSettings.TabName == tabNames[13]) {
            return <JobMaster />
        } else if (tabSettings.TabName == tabNames[14]) {
            return <OrgDevices />
        } else if (tabSettings.TabName == tabNames[15]) {
            return <Reports />
        } else if (tabSettings.TabName == tabNames[16]) {
            return <RoleDetails />
        } else if (tabSettings.TabName == tabNames[17]) {
            return <UserRolesMapping />
        } else if (tabSettings.TabName == tabNames[18]) {
            return <Settings />
        }
    }

    render() {
        return (
            <div className="wrapper">
                <Header />
                <SideBar />
                <div className="content-wrapper" style={{ padding: '10px' }}>
                    {this.renderPage()}
                </div>
                <Footer />
            </div>
        )
    }
}

const mapStateToProps = state => ({
    currentUserFromStore: state.userReducer.currentUser,
    gameDataFromStore: state.gameReducer.gameList,
    tabSettings: state.userReducer.tabSettings,
    tabNames: state.userReducer.tabNames,
})

export default connect(
    mapStateToProps,
    null
)(AdminContent)
