import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class Config extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Config Id',
                },
                {
                    field: 'configName',
                    headerName: 'Config Name',
                },
                {
                    field: 'configValue',
                    headerName: 'Config Value',
                },
                {
                    field: 'configStatus',
                    headerName: 'Config Status',
                    valueGetter: function (param) {
                        return param.data.configStatus == 0 ? 'InActive' : 'Active'
                    },
                    cellStyle: function (param) {
                        if (param.data.configStatus == 1) {
                            return { color: 'green' };
                        } else {
                            return { color: 'red' };
                        }
                    }
                },
                {
                    field: 'orgName',
                    headerName: 'Organization',
                }
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorConfig: null,
            orgList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            configName: '',
            configValue: '',
            configStatus: '',
            orgId: null,
            selectedOrg: null,

        }
        this.onCreateConfig = this.onCreateConfig.bind(this)
        this.onAddEditConfig = this.onAddEditConfig.bind(this)
        this.onDeleteConfig = this.onDeleteConfig.bind(this)
        this.onEditConfig = this.onEditConfig.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadConfig()
        this.loadOrganization()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadConfig() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'configs')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadOrganization() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'organizations')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    orgList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateConfig() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            configName,
            configValue,
            configStatus,
            orgId

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'configs')
            .send({
                configName,
                configValue,
                configStatus,
                orgId
            })
        const response = r.body

        if (response.success) {
            this.setState({
                configName: '',
                configValue: '',
                configStatus: '',
                orgId: null,
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadConfig()
                confirmAlert({
                    title: 'Config',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Config',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditConfig() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            configName,
            configValue,
            configStatus,
            orgId

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'configs/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                configName,
                configValue,
                configStatus,
                orgId

            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadConfig()
                confirmAlert({
                    title: 'Config',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Config',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearConfig = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditConfig(type) {
        const { selectedRow, orgList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update Config',
                    id: selectedRow.id,
                    configName: selectedRow.configName,
                    configValue: selectedRow.configValue,
                    configStatus: selectedRow.configStatus,
                    orgId: selectedRow.orgId,
                    selectedOrg: { id: selectedRow.orgId, orgName: orgList.find(x => x.id === selectedRow.orgId).orgName }

                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add Config',
                    id: null,
                    configName: '',
                    configValue: '',
                    configStatus: '',
                    orgId: null,
                })
            }
        }
    }

    async onDeleteConfig() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete Config',
            message: "Are you sure you wants to delete the record : " + selectedRow.configName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteConfig()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteConfig() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'configs/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                configName: '',
                configValue: '',
                configStatus: '',
                orgId: null,
                selectedRow: null
            }, () => {
                this.loadConfig()
                confirmAlert({
                    title: 'Config',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Config',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    onCheckboxChange = (e) => {
        this.setState({
            [e.target.id]: e.target.checked ? 1 : 0
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    onEditorConfigChange = (editorConfig) => {
        this.setState({
            editorConfig,
            description: editorConfig && draftToMarkdown(convertToRaw(editorConfig.getCurrentContent())),
        })
    }

    onChangeOrg = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedOrg: newValue,
                orgId: newValue.id
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditConfig(type)
        })
    }

    render() {
        const { isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteConfig}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditConfig.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditConfig.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearConfig()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}

                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="configName">ConfigName </label>
                                        <input type="text" onChange={this.onTextChange} id="configName" value={this.state.configName} className="form-control" />
                                        {this.validator.message('configName', this.state.configName, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="configValue">Config Value </label>
                                        <input type="text" onChange={this.onTextChange} id="configValue" value={this.state.configValue} className="form-control" />
                                        {this.validator.message('configValue', this.state.configValue, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgId">Organization </label>
                                        <Select
                                            // className="form-control"
                                            id={"org"}
                                            value={this.state.selectedOrg}
                                            onChange={this.onChangeOrg}
                                            options={this.state.orgList}
                                            getOptionLabel={(option) => option.orgName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedOrg', this.state.selectedOrg, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="configStatus">Config Status </label>
                                        <div className="custom-control custom-switch">
                                            <input type="checkbox" className="custom-control-input"
                                                onChange={this.onCheckboxChange} id="configStatus"
                                                value={this.state.configStatus}
                                                checked={this.state.configStatus == 1 ? true : false}
                                            />
                                            <label className="custom-control-label" for="configStatus"></label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                <button
                                    type="button"
                                    className="btn btn-secondary float-right mx-1"
                                    onClick={() => {
                                        this.setState({ isPopupOpen: false }, () => {
                                            this.clearConfig()
                                        })
                                    }}
                                >{'Cancel'}</button>
                                <button
                                    type="button"
                                    className="btn btn-info float-right  mx-1"
                                    onClick={this.state.actionType == 'Update Config' ? this.onEditConfig : this.onCreateConfig}
                                >{'Submit'}</button>
                            </div>
                        </div>
                    </section>
                </div>
            </LoadingOverlay>
        )
    }
}

const mapConfigToProps = vendor => ({
    tabSettings: vendor.userReducer.tabSettings,
})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapConfigToProps,
    mapDispatchToProps
)(Config)
