import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class Users extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            selectedRoleCard: null,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'id',
                    maxWidth: 150,
                },
                {
                    field: 'username',
                    headerName: 'Username',
                    maxWidth: 150,
                },
                {
                    field: 'usersFirstName',
                    headerName: 'FirstName',
                    maxWidth: 150
                },
                {
                    field: 'usersLastName',
                    headerName: 'LastName',
                    maxWidth: 150,
                },
                {
                    field: 'roleName',
                    headerName: 'Roles',
                    cellStyle: { 'white-space': 'normal' },
                    maxWidth: 150
                },
                {
                    field: 'email',
                    headerName: 'Email Address',
                    cellStyle: { 'white-space': 'normal' }
                }
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorState: null,
            organisation: '',
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,
            username: "",
            password: "",
            confirm_password: "",
            usersFirstName: "",
            usersLastName: "",
            email: "",
            roleId: null,
            age: "",
            phone: "",
            website: "",
            addressId: "",
            salutation: "",
            salutations: null,
            SalutationList: [{ label: 'Mr.', value: 'Mr.' }, { label: 'Mrs.', value: 'Mrs.' }]
        }
        this.onCreateUser = this.onCreateUser.bind(this)
        this.onAddEditUser = this.onAddEditUser.bind(this)
        this.onDeleteUser = this.onDeleteUser.bind(this)
        this.onEditUserName = this.onEditUserName.bind(this)
        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadUsers()
        this.loadUserRoles()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadUsers() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'users')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadUserRoles() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'roles')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    userRoleList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateUser() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }

        const {
            selectedUserRole,
            username,
            password,
            confirm_password,
            usersFirstName,
            usersLastName,
            email,
            roleId,
            age,
            phone,
            website,
            addressId,
            salutation,
            salutations
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'users')
            .send({
                username: usersFirstName + Math.floor(Math.random() * 23434),
                password: password,
                confirm_password: password,
                usersFirstName: usersFirstName,
                usersLastName: usersLastName,
                email: email,
                roleId: roleId,
                age: 20,
                phone: phone,
                website: website,
                addressId: addressId,
                salutation: salutations.value,
                isDeleted: false
            })
        const response = r.body

        if (response.success) {
            this.setState({
                name: "",
                organisationId: "",
                description: "",
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadUsers()
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditUserName() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedUserRole,
            selectedSequence,
            selectedRow,
            id,
            usersFirstName,
            usersLastName,
            email,
            phone,
            website,
            salutation,
            addressId,
            password,
            username,
            roleId,
            confirm_password,
            age,
            salutations
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'users/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                password: 'test@123',
                confirm_password: 'test@123',
                usersFirstName,
                usersLastName,
                email,
                roleId: selectedUserRole.id,
                age,
                phone,
                website,
                addressId,
                salutation: salutations.value,
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadUsers()
                confirmAlert({
                    title: 'User',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'User',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearState = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onRowDoubleClicked(type) {

        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditUser(type)
        })
    }

    async onAddEditUser(type) {
        const { selectedRow, userRoleList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {

            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update User',
                    id: selectedRow.id,
                    usersFirstName: selectedRow.usersFirstName,
                    usersLastName: selectedRow.usersLastName,
                    email: selectedRow.email,
                    phone: selectedRow.phone,
                    website: selectedRow.website,
                    salutations: { label: selectedRow.salutation, value: selectedRow.salutation },
                    addressId: selectedRow.addressId,
                    username: selectedRow.username,
                    roleId: selectedRow.roleId,
                    age: selectedRow.age,
                    selectedUserRole: { id: selectedRow.roleId, roleName: userRoleList.find(x => x.id === selectedRow.roleId).roleName }
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    id: '',
                    selectedUserRole: null,
                    selectedSequence: null,
                    isDeleted: false,
                    actionType: 'Add User',
                    usersFirstName: "",
                    usersLastName: "",
                    email: "",
                    phone: "",
                    website: "",
                    salutations: null,
                    addressId: "",
                    password: "",
                    username: "",
                    roleId: null,
                    confirm_password: "",
                    age: 21
                })
            }
        }
    }

    async onDeleteUser() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete RoleName',
            message: "Are you sure you wants to delete the record : " + selectedRow.usersFirstName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteUser()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteUser() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'users/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                name: "",
                description: "",
                isLoading: false,
                selectedRow: null
            }, () => {
                this.loadUsers()
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleChange = selectedRoleCard => {
        this.setState(
            { selectedRoleCard },
            () => console.log(`Option selected:`, this.state.selectedRoleCard)
        );
    }

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            description: editorState && draftToMarkdown(convertToRaw(editorState.getCurrentContent())),
        })
    }

    onUserRoleChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            debugger
            this.setState({
                selectedUserRole: newValue,
                roleId: newValue.id,
            })
        }
    }

    onSalutationChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                salutations: newValue,
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    render() {
        const { isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div className="row mb-2">
                                        <div className="col-sm-6">
                                            <h4 className="m-0 text-dark">{this.state.actionType}</h4>
                                        </div>
                                        <div className="col-sm-6 float-sm-right">
                                            {/* <ol className="breadcrumb float-sm-right"> */}
                                            <div style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                                <button
                                                    disabled={!this.state.selectedRow}
                                                    type="button"
                                                    className="btn btn-danger float-right"
                                                    onClick={this.onDeleteUser}
                                                >{'Delete Record'}</button>
                                                <button
                                                    disabled={!this.state.selectedRow}
                                                    type="button"
                                                    className="btn btn-info float-right  mx-2"
                                                    onClick={this.onAddEditUser.bind(this, 'edit')}
                                                >{'Update Record'}</button>
                                                <button
                                                    type="button"
                                                    className="btn btn-info float-right "
                                                    onClick={this.onAddEditUser.bind(this, 'add')}
                                                >{'Add New'}</button>
                                            </div>
                                            {/* <div style={{  display: !isPopupOpen ? 'none' : 'block' }}>
                                                    <strong className="text-center">{this.state.actionType} </strong>
                                                    <button
                                                        type="button"
                                                        className="btn btn-secondary float-right mx-1"
                                                        onClick={() => {
                                                            this.setState({ isPopupOpen: false }, () => {
                                                                this.clearState()
                                                            })
                                                        }}
                                                    >{'Back'}</button>
                                                </div> */}
                                            {/* </ol> */}
                                        </div>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                    rowDeselection={true}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="form-group">
                                                <label style={{ fontWeight: 100 }} htmlFor="Salutation">Salutation</label>
                                                <CreatableSelect
                                                    isClearable
                                                    value={this.state.salutations}
                                                    onChange={this.onSalutationChange}
                                                    onInputChange={this.handleInputChange}
                                                    options={this.state.SalutationList}
                                                />
                                                {this.validator.message('salutations', this.state.salutations, 'required', { className: 'text-danger small-font14' })}
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <div className="form-group">
                                                <label style={{ fontWeight: 100 }} htmlFor="UserRole">User Role</label>
                                                <Select
                                                    id={"roles"}
                                                    value={this.state.selectedUserRole}
                                                    onChange={this.onUserRoleChange}
                                                    options={this.state.userRoleList}
                                                    getOptionLabel={(option) => option.roleName}
                                                    getOptionValue={(option) => option.id}
                                                />
                                                {this.validator.message('selectedUserRole', this.state.selectedUserRole, 'required', { className: 'text-danger small-font14' })}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 100 }} htmlFor="usersFirstName">First Name </label>
                                        <input type="text" onChange={this.onTextChange} id="usersFirstName" value={this.state.usersFirstName} className="form-control" />
                                        {this.validator.message('usersFirstName', this.state.usersFirstName, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 100 }} htmlFor="usersLastName">Last Name </label>
                                        <input type="text" onChange={this.onTextChange} id="usersLastName" value={this.state.usersLastName} className="form-control" />
                                        {this.validator.message('usersLastName', this.state.usersLastName, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 100 }} htmlFor="email">Email Address </label>
                                        <input type="text" onChange={this.onTextChange} id="email" value={this.state.email} className="form-control" />
                                        {this.validator.message('email', this.state.email, 'required|email', { className: 'text-danger' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    {
                                        this.state.actionType == 'Add User' &&
                                        <div className="form-group">
                                            <label style={{ fontWeight: 100 }} htmlFor="password">Password </label>
                                            <input type="password" onChange={this.onTextChange} id="password" value={this.state.password} className="form-control" />
                                            {this.validator.message('password', this.state.password, 'required', { className: 'text-danger small-font14' })}
                                        </div>
                                    }
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 100 }} htmlFor="phone">Phone </label>
                                        <input type="text" onChange={this.onTextChange} id="phone" value={this.state.phone} className="form-control" />
                                        {this.validator.message('phone', this.state.phone, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearState()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update User' ? this.onEditUserName : this.onCreateUser}
                            >{'Submit'}</button>
                        </div>
                    </section>
                </div>
            </LoadingOverlay>
        )
    }
}

const mapStateToProps = state => ({
    tabSettings: state.userReducer.tabSettings,
})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Users)
