import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import LoadingOverlay from 'react-loading-overlay';
import BootstrapSwitchButton from 'bootstrap-switch-button-react'

class RoleDetails extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            roleDetailList: [],

            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorRoleDetail: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            roleEntityId: null,
            roleId: null,
            entityRead: '',
            entityAdd: '',
            entityUpdate: '',
            entityPrint: '',
            entityExport: '',
            entityDelete: '',
            selectedRoleEntity: null,
            roleEntityList: []

        }
        this.onCreateRoleDetail = this.onCreateRoleDetail.bind(this)
        this.onEditRoleDetail = this.onEditRoleDetail.bind(this)
        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        const { tabSettings } = this.props
        if (tabSettings.roleId) {
            this.loadRoleDetails()
        } else {
            this.props.updateTabNavigation({ TabName: 'Roles' })
        }
    }

    async loadRoleDetails() {
        const { tabSettings } = this.props
        this.setState({
            isLoading: true
        })

        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'roledetails/id/' + tabSettings.roleId)
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateRoleDetail() {
        const {
            roleId,
            roleEntityId,
            entityRead,
            entityAdd,
            entityUpdate,
            entityPrint,
            entityExport,
            entityDelete,
        } = this.state

        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'roledetails')
            .send({
                roleId,
                roleEntityId,
                entityRead,
                entityAdd,
                entityUpdate,
                entityPrint,
                entityExport,
                entityDelete,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                roleEntityId: null,
                entityRead: '',
                entityAdd: '',
                entityUpdate: '',
                entityPrint: '',
                entityExport: '',
                entityDelete: '',

                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadRoleDetails()
                // confirmAlert({
                //     title: 'RoleDetails',
                //     message: response.message,
                //     buttons: [
                //         {
                //             label: 'Ok',
                //             onClick: () => {

                //             }
                //         }
                //     ],
                //     closeOnClickOutside: false,
                //     closeOnEscape: false
                // })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'RoleDetails',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditRoleDetail() {
        const {
            id,
            roleId,
            roleEntityId,
            entityRead,
            entityAdd,
            entityUpdate,
            entityPrint,
            entityExport,
            entityDelete,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'roledetails/id/' + id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                roleId,
                roleEntityId,
                entityRead,
                entityAdd,
                entityUpdate,
                entityPrint,
                entityExport,
                entityDelete,

            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadRoleDetails()
                // confirmAlert({
                //     title: 'RoleDetails',
                //     message: response.message,
                //     buttons: [
                //         {
                //             label: 'Ok',
                //             onClick: () => {

                //             }
                //         }
                //     ],
                //     closeOnClickOutside: false,
                //     closeOnEscape: false
                // })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'RoleDetails',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onCheckboxChange = (roleEntityObj, objIndex, entityName, checked,) => {
        const { rowData } = this.state
        const { tabSettings } = this.props

        this.setState({
            id: roleEntityObj.id,
            roleId: tabSettings.roleId,
            roleEntityId: roleEntityObj.roleEntityId,
            entityRead: rowData[objIndex].entityRead,
            entityAdd: rowData[objIndex].entityAdd,
            entityUpdate: rowData[objIndex].entityUpdate,
            entityPrint: rowData[objIndex].entityPrint,
            entityExport: rowData[objIndex].entityExport,
            entityDelete: rowData[objIndex].entityDelete,

            [entityName]: checked ? 1 : 0,
        }, () => {

            if (roleEntityObj.id) {
                this.onEditRoleDetail()
            } else {
                this.onCreateRoleDetail()
            }
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    render() {
        const { roleDetailList, isLoading, isPopupOpen } = this.state
        const { tabSettings } = this.props
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card">
                    <section className="content" >
                        <div className="card-body">
                            <div className="row card-header">
                                <div className="col-md-12">
                                    <div className="card-body">
                                        <strong className="text-center">{'Manage Role Permissions'} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right"
                                            onClick={() => this.props.updateTabNavigation({ TabName: 'Roles' })}
                                        >{'Back'}</button>
                                    </div>
                                </div>
                            </div>
                            <div className="row card-header">
                                <div className="col-md-2">
                                    <div className="form-group">
                                        <label htmlFor="entityDelete">Role Name </label>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="form-group">
                                        <input readOnly type="text" value={tabSettings.roleName} className="form-control" />
                                    </div>
                                </div>
                            </div>
                            <div className="row card-header">
                                <div className="col-md-4">
                                    <label htmlFor="name">Entity Name</label>
                                </div>
                                <div className="col-md-1">
                                    <label htmlFor="add">Add </label>
                                </div>
                                <div className="col-md-1">
                                    <label htmlFor="view">View</label>
                                </div>
                                <div className="col-md-1">
                                    <label htmlFor="update">Update</label>
                                </div>
                                <div className="col-md-1">
                                    <label htmlFor="delete">Delete</label>
                                </div>
                                <div className="col-md-1">
                                    <label htmlFor="print">Print</label>
                                </div>
                                <div className="col-md-1">
                                    <label htmlFor="export">Export</label>
                                </div>
                            </div>
                            {this.state.rowData.map((value, indx) => {
                                console.log('value>>>', value)
                                return (
                                    <div className="row card-header">
                                        <div className="col-md-4">
                                            <label style={{ fontWeight: 200 }} htmlFor="name">{value.entityName}</label>
                                        </div>
                                        <div className="col-md-1">
                                            <BootstrapSwitchButton
                                                value={value.entityAdd}
                                                checked={value.entityAdd == 1 ? true : false}
                                                onlabel='ON'
                                                offlabel='OFF'
                                                size="sm"
                                                onChange={(checked) => { this.onCheckboxChange(value, indx, 'entityAdd', checked) }} id={value.entityName + '-entityAdd'}
                                            />
                                        </div>
                                        <div className="col-md-1">
                                            <BootstrapSwitchButton
                                                value={value.entityRead}
                                                checked={value.entityRead == 1 ? true : false}
                                                onlabel='ON'
                                                offlabel='OFF'
                                                size="sm"
                                                onChange={(checked) => { this.onCheckboxChange(value, indx, 'entityRead', checked) }} id={value.entityName + '-entityRead'}
                                            />
                                        </div>
                                        <div className="col-md-1">
                                            <BootstrapSwitchButton
                                                value={value.entityUpdate}
                                                checked={value.entityUpdate == 1 ? true : false}
                                                onlabel='ON'
                                                offlabel='OFF'
                                                size="sm"
                                                onChange={(checked) => { this.onCheckboxChange(value, indx, 'entityUpdate', checked) }} id={value.entityName + '-entityUpdate'}
                                            />
                                        </div>
                                        <div className="col-md-1">
                                            <BootstrapSwitchButton
                                                value={value.entityDelete}
                                                checked={value.entityDelete == 1 ? true : false}
                                                onlabel='ON'
                                                offlabel='OFF'
                                                size="sm"
                                                onChange={(checked) => { this.onCheckboxChange(value, indx, 'entityDelete', checked) }} id={value.entityName + '-entityDelete'}
                                            />
                                        </div>
                                        <div className="col-md-1">
                                            <BootstrapSwitchButton
                                                value={value.entityPrint}
                                                checked={value.entityPrint == 1 ? true : false}
                                                onlabel='ON'
                                                offlabel='OFF'
                                                size="sm"
                                                onChange={(checked) => { this.onCheckboxChange(value, indx, 'entityPrint', checked) }} id={value.entityName + '-entityPrint'}
                                            />
                                        </div>
                                        <div className="col-md-1">
                                            <BootstrapSwitchButton
                                                value={value.entityExport}
                                                checked={value.entityExport == 1 ? true : false}
                                                onlabel='ON'
                                                offlabel='OFF'
                                                size="sm"
                                                onChange={(checked) => { this.onCheckboxChange(value, indx, 'entityExport', checked) }} id={value.entityName + '-entityExport'}
                                            />
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </section >
                </div >
            </LoadingOverlay >
        )
    }
}

const mapRoleDetailToProps = roleDetail => ({
    tabSettings: roleDetail.userReducer.tabSettings,
    roleDetailList: roleDetail.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapRoleDetailToProps,
    mapDispatchToProps
)(RoleDetails)
