import React, { Component } from 'react';
import { connect } from 'react-redux'
import request from 'superagent';
// import { API_URL, IS_PRODUCTION, API_PORT } from '../constants'
import { updateTabNavigation } from '../store/actions'
import { Card, Nav } from 'react-bootstrap'
import LoadingOverlay from 'react-loading-overlay';

import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import AdminContent from '../components/AdminContent';
import { Redirect } from 'react-router-dom';

class Login extends Component {

    constructor(props) {
        super(props)
        this.state = {
            gameName: 'NCM',
            email: '',
            password: '',
            isLoading: false
        }

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    async onLoginClick() {
        if (this.state.email && this.state.password) {
            await this.loginWithTeam()
        } else {
            this.showAlert("Credientials can't blank !!")
        }
    }

    showAlert = (msg) => {
        confirmAlert({
            title: 'Login',
            message: msg,
            buttons: [
                {
                    label: 'Ok',
                    onClick: () => {
                        // this.props.undo()
                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async loginWithTeam() {

        this.setState({
            isLoading: true
        })

        try {
            const r = await request.post(process.env.REACT_APP_API_URL + 'users/login')
                .send({
                    email: this.state.email,
                    password: this.state.password
                })
            const response = r.body

            if (response.success) {

                this.setState({
                    isLoading: false
                }, () => {
                    localStorage.setItem('adminLogin', true);
                    localStorage.setItem('admin_access_token', response.ResponsePacket.token);

                    this.props.history.push('/admin');
                })
            } else {
                this.setState({
                    isLoading: false
                }, () => {
                    this.showAlert(response.message)
                })
            }
        } catch (catchErr) {
            this.showAlert("Error:", catchErr.error)
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    render() {
        return (
            <LoadingOverlay active={this.state.isLoading} spinner >
                <section className="hold-transition login-page">
                    <div className="login-box">
                        <div className="login-logo">
                            <a href='javascript:void(0)'><b></b> &nbsp;NCM</a>
                        </div>
                        <div className="card">
                            <div className="card-body login-card-body">
                                <p className="login-box-msg">Sign in to start your session</p>
                                <form>
                                    <div className="input-group mb-3">
                                        <input type="text" onChange={this.onTextChange} id="email" value={this.state.email} className="form-control" placeholder="email" />
                                        <div className="input-group-append">
                                            {/* <div className="input-group-text">
                                                <span className="fas fa-envelope"></span>
                                            </div> */}
                                        </div>
                                    </div>
                                    <div className="input-group mb-3">
                                        <input type="password" className="form-control" onChange={this.onTextChange} id="password" value={this.state.password} placeholder="Password" />
                                        <div className="input-group-append">
                                            {/* <div className="input-group-text">
                                                <span className="fas fa-lock"></span>
                                            </div> */}
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-8">
                                        </div>
                                        <div className="col-4">
                                            <button type="button" onClick={this.onLoginClick.bind(this)} className="btn btn-primary btn-block">Sign In</button>
                                        </div>
                                    </div>
                                </form>
                                <div className="social-auth-links text-center mb-3">
                                    <p>- OR -</p>
                                    <a href="javascript:void(0)" className="btn btn-block btn-primary">
                                        <i className="fab fa-facebook mr-2"></i> Sign in using Facebook
                                    </a>
                                    <a href="javascript:void(0)" className="btn btn-block btn-danger">
                                        <i className="fab fa-google-plus mr-2"></i> Sign in using Google+
                                    </a>
                                </div>
                                <p className="mb-1">
                                    <a href="javascript:void(0)">I forgot my password</a>
                                </p>
                                <p className="mb-0">
                                    <a href="javascript:void(0)" className="text-center">Register a new membership</a>
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </LoadingOverlay>
        )
    }
}

const mapStateToProps = state => ({
    tabSettings: state.userReducer.tabSettings,
    tabNames: state.userReducer.tabNames,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Login)
