import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class Devices extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            selectedRoleCard: null,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Device Id',
                },
                {
                    field: 'vendorName',
                    headerName: 'Vendor',
                },
                {
                    field: 'deviceType',
                    headerName: 'Device Type',
                },
                {
                    field: 'deviceModel',
                    headerName: 'Device Model',
                },
                {
                    field: 'deviceStatus',
                    headerName: 'Device Status',
                    valueGetter: function (param) {
                        return param.data.deviceStatus == 0 ? 'InActive' : 'Active'
                    },
                    cellStyle: function (param) {
                        if (param.data.deviceStatus == 1) {
                            return { color: 'green' };
                        } else {
                            return { color: 'red' };
                        }
                    }
                },
                {
                    field: 'deviceDescription',
                    headerName: 'Device Description',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorState: null,
            isPopupOpen: false,
            actionType: null,

            deviceType: "",
            vendorId: null,
            deviceDescription: "",
            deviceModel: "",
            deviceStatus: "",

            selectedVendor: null,
            vendorList: []

        }
        this.onCreateDevice = this.onCreateDevice.bind(this)
        this.onAddEditDevice = this.onAddEditDevice.bind(this)
        this.onDeleteDevice = this.onDeleteDevice.bind(this)
        this.onEditDevice = this.onEditDevice.bind(this)
        this.validator = new SimpleReactValidator({ autoForceUpdate: this })
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadDevices()
        this.loadVendors()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadDevices() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'devices')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadVendors() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'vendors')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    vendorList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }

    }

    async onCreateDevice() {

        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }

        const {
            deviceType,
            vendorId,
            deviceDescription,
            deviceModel,
            deviceStatus,
            selectedVendor
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'devices')
            .send({
                deviceType,
                deviceDescription,
                deviceModel,
                deviceStatus,
                vendorId: selectedVendor.value
            })
        const response = r.body

        if (response.success) {
            this.setState({
                deviceType: '',
                deviceDescription: '',
                deviceModel: '',
                deviceStatus: '',
                vendorId: null,
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadDevices()
                confirmAlert({
                    title: 'Devices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Devices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditDevice() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            deviceType,
            deviceDescription,
            deviceModel,
            deviceStatus,
            vendorId,
            selectedVendor
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'devices/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                deviceType,
                deviceDescription,
                deviceModel,
                deviceStatus,
                vendorId: selectedVendor.value
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadDevices()
                confirmAlert({
                    title: 'Devices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Devices',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearState = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditDevice(type) {
        const { selectedRow, vendorList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update Device',
                    id: selectedRow.id,
                    deviceType: selectedRow.deviceType,
                    deviceDescription: selectedRow.deviceDescription,
                    deviceModel: selectedRow.deviceModel,
                    deviceStatus: selectedRow.deviceStatus,
                    vendorId: selectedRow.vendorId,
                    selectedVendor: { id: selectedRow.vendorId, vendorName: vendorList.find(x => x.id === selectedRow.vendorId).vendorName }

                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add Device',
                    id: null,
                    deviceType: '',
                    deviceDescription: '',
                    deviceModel: '',
                    deviceStatus: '',
                    vendorId: null,
                })
            }
        }
    }

    async onDeleteDevice() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete Device',
            message: "Are you sure you wants to delete the record : " + selectedRow.deviceType + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteDevice()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteDevice() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'devices/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                deviceType: '',
                deviceDescription: '',
                deviceModel: '',
                deviceStatus: '',
                vendorId: null,
                selectedRow: null
            }, () => {
                this.loadDevices()
                confirmAlert({
                    title: 'State',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'State',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleChange = selectedRoleCard => {
        this.setState(
            { selectedRoleCard },
            () => console.log(`Option selected:`, this.state.selectedRoleCard)
        );
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            description: editorState && draftToMarkdown(convertToRaw(editorState.getCurrentContent())),
        })
    }

    onVendorChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedVendor: newValue,
                vendorId: newValue.id
            })
        }
    }


    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditDevice(type)
        })
    }

    onCheckboxChange = (e) => {
        this.setState({
            [e.target.id]: e.target.checked ? 1 : 0
        })
    }


    render() {
        const { vendorList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteDevice}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditDevice.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditDevice.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearState()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="Vendor">Vendor</label>
                                        <Select
                                            id={"country"}
                                            value={this.state.selectedVendor}
                                            onChange={this.onVendorChange}
                                            options={vendorList}
                                            getOptionLabel={(option) => option.vendorName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedVendor', this.state.selectedVendor, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceType">Device Type </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceType" value={this.state.deviceType} className="form-control" />
                                        {this.validator.message('deviceType', this.state.deviceType, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="deviceModel">Device Model </label>
                                        <input type="text" onChange={this.onTextChange} id="deviceModel" value={this.state.deviceModel} className="form-control" />
                                        {this.validator.message('deviceModel', this.state.deviceModel, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <div className="form-group">
                                            <label style={{ fontWeight: 200 }} htmlFor="deviceStatus">Device Status </label>
                                            <div className="custom-control custom-switch">
                                                <input type="checkbox" className="custom-control-input"
                                                    onChange={this.onCheckboxChange} id="deviceStatus"
                                                    value={this.state.deviceStatus}
                                                    checked={this.state.deviceStatus == 1 ? true : false}
                                                />
                                                <label className="custom-control-label" for="deviceStatus"></label>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="form-group">
                                <label style={{ fontWeight: 200 }} htmlFor="deviceDescription">Device Description </label>
                                <textarea onChange={this.onTextChange} id="deviceDescription" value={this.state.deviceDescription} className="form-control" />
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearState()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update Device' ? this.onEditDevice : this.onCreateDevice}
                            >{'Submit'}</button>
                        </div>
                    </section >
                </div>
            </LoadingOverlay >
        )
    }
}

const mapStateToProps = state => ({
    tabSettings: state.userReducer.tabSettings

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Devices)
