import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import SimpleReactValidator from 'simple-react-validator';

class UserRolesMapping extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            UserRolesMappingList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'RolesMapping Id',
                },
                {
                    field: 'userId',
                    headerName: 'User Id',
                },
                {
                    field: 'roleId',
                    headerName: 'RoleId',
                },
                {
                    field: 'orgName',
                    headerName: 'Organization',
                }
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            editorUserRolesMapping: null,

            rowData: [],
            userRoleList: [],
            userList: [],
            orgList: [],

            selectedOrg: null,
            selectedRole: null,
            selectedUser: null,

            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,
            userId: null,
            roleId: null,
            orgId: null,
            selectedCountry: null,

        }
        this.onCreateUserRolesMapping = this.onCreateUserRolesMapping.bind(this)
        this.onAddEditUserRolesMapping = this.onAddEditUserRolesMapping.bind(this)
        this.onDeleteUserRolesMapping = this.onDeleteUserRolesMapping.bind(this)
        this.onEditUserRolesMapping = this.onEditUserRolesMapping.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadUserRolesMapping()
        this.loadUsers()
        this.loadUserRoles()
        this.loadOrganizations()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadUserRolesMapping() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'userrolesmapping')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadUsers() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'users')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    userList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadUserRoles() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'roles')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    userRoleList: response.ResponsePacket,
                    isLoading: false
                })

            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadOrganizations() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'organizations')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    orgList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateUserRolesMapping() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            userId,
            roleId,
            orgId,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'userrolesmapping')
            .send({
                userId,
                roleId,
                orgId,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                userId: null,
                roleId: null,
                orgId: null,
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadUserRolesMapping()
                confirmAlert({
                    title: 'UserRolesMapping',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'UserRolesMapping',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditUserRolesMapping() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            userId,
            roleId,
            orgId,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'userrolesmapping/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                userId,
                roleId,
                orgId,

            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadUserRolesMapping()
                confirmAlert({
                    title: 'UserRolesMapping',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'UserRolesMapping',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearUserRolesMapping = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditUserRolesMapping(type) {
        const { selectedRow, userList, userRoleList, orgList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update UserRolesMapping',
                    id: selectedRow.id,
                    userId: selectedRow.userId,
                    roleId: selectedRow.roleId,
                    orgId: selectedRow.orgId,
                    selectedUser: { id: selectedRow.userId, usersFirstName: userList.find(x => x.id === selectedRow.userId).usersFirstName },
                    selectedRole: { id: selectedRow.roleId, roleName: userRoleList.find(x => x.id === selectedRow.roleId).roleName },
                    selectedOrg: { id: selectedRow.orgId, orgName: orgList.find(x => x.id === selectedRow.orgId).orgName }

                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add UserRolesMapping',
                    id: null,
                    userId: null,
                    roleId: null,
                    orgId: null,
                })
            }
        }
    }

    async onDeleteUserRolesMapping() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete UserRolesMapping',
            message: "Are you sure you wants to delete the record : " + selectedRow.id + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteUserRolesMapping()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteUserRolesMapping() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'userrolesmapping/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                userId: null,
                roleId: null,
                orgId: null,
            }, () => {
                this.loadUserRolesMapping()
                confirmAlert({
                    title: 'UserRolesMapping',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'UserRolesMapping',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleChangeUsers = selectedUser => {

        this.setState({
            selectedUser,
            userId: selectedUser.id
        })
    }

    handleChangeRoles = selectedRole => {
        this.setState({
            selectedRole,
            roleId: selectedRole.id
        })
    }

    handleChangeOrg = selectedOrg => {
        this.setState({
            selectedOrg,
            orgId: selectedOrg.id
        })
    }

    onEditorUserRolesMappingChange = (editorUserRolesMapping) => {
        this.setState({
            editorUserRolesMapping,
            description: editorUserRolesMapping && draftToMarkdown(convertToRaw(editorUserRolesMapping.getCurrentContent())),
        })
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditUserRolesMapping(type)
        })
    }

    render() {
        const { UserRolesMappingList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteUserRolesMapping}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditUserRolesMapping.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditUserRolesMapping.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearUserRolesMapping()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}

                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="userId">User Id </label>
                                        <Select
                                            id={"user"}
                                            value={this.state.selectedUser}
                                            onChange={this.handleChangeUsers}
                                            options={this.state.userList}
                                            getOptionLabel={(option) => option.usersFirstName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedUser', this.state.selectedUser, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="roleId">Role Id </label>
                                        <Select
                                            id={"roleName"}
                                            value={this.state.selectedRole}
                                            onChange={this.handleChangeRoles}
                                            options={this.state.userRoleList}
                                            getOptionLabel={(option) => option.roleName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedRole', this.state.selectedRole, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="orgId">Org Id </label>
                                        <Select
                                            id={"org"}
                                            value={this.state.selectedOrg}
                                            onChange={this.handleChangeOrg}
                                            options={this.state.orgList}
                                            getOptionLabel={(option) => option.orgName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedOrg', this.state.selectedOrg, 'required', { className: 'text-danger small-font14' })}

                                    </div>
                                </div>
                            </div>

                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearUserRolesMapping()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update UserRolesMapping' ? this.onEditUserRolesMapping : this.onCreateUserRolesMapping}
                            >{'Submit'}</button>
                        </div>
                    </section >
                </div >
            </LoadingOverlay >
        )
    }
}

const mapUserRolesMappingToProps = UserRolesMapping => ({
    tabSettings: UserRolesMapping.userReducer.tabSettings,
})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapUserRolesMappingToProps,
    mapDispatchToProps
)(UserRolesMapping)
