import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class Countries extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            countryList: [],
            selectedRoleCard: null,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Country Id',
                },
                {
                    field: 'countryName',
                    headerName: 'Country Name',
                },
                {
                    field: 'countryStatus',
                    headerName: 'Country Status',
                    valueGetter: function (param) {
                        return param.data.countryStatus == 0 ? 'InActive' : 'Active'
                    },
                    cellStyle: function (param) {
                        if (param.data.countryStatus == 1) {
                            return { color: 'green' };
                        } else {
                            return { color: 'red' };
                        }
                    }
                },
                {
                    field: 'phoneCode',
                    headerName: 'Phone Code',

                },
                {
                    field: 'flag',
                    headerName: 'Flag',
                }

            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorState: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            countryName: "",
            countryStatus: "",
            phoneCode: "",
            flag: "",

        }
        this.onCreateCountry = this.onCreateCountry.bind(this)
        this.onAddEditCountry = this.onAddEditCountry.bind(this)
        this.onDeleteCountry = this.onDeleteCountry.bind(this)
        this.onEditCountry = this.onEditCountry.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadContries()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadContries() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'countries')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    countryList: response.ResponsePacket,
                    rowData: response.ResponsePacket,
                    isLoading: false
                }, () => {
                    console.log(this.state.countryList)
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }

    }

    async onCreateCountry() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            countryName,
            countryStatus,
            phoneCode,
            flag

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'countries')
            .send({
                countryName,
                countryStatus,
                phoneCode,
                flag
            })
        const response = r.body

        if (response.success) {
            this.setState({
                countryName: '',
                countryStatus: 0,
                phoneCode: '',
                flag: '',
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadContries()
                confirmAlert({
                    title: 'Countries',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Countries',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditCountry() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            countryName,
            countryStatus,
            phoneCode,
            flag
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'countries/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                countryName,
                countryStatus,
                phoneCode,
                flag
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadContries()
                confirmAlert({
                    title: 'Countries',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Countries',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearState = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditCountry(type) {
        const { selectedRow } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update Country',
                    id: selectedRow.id,
                    countryName: selectedRow.countryName,
                    countryStatus: selectedRow.countryStatus,
                    phoneCode: selectedRow.phoneCode,
                    flag: selectedRow.flag
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add Country',
                    id: null,
                    countryName: '',
                    countryStatus: '',
                    phoneCode: '',
                    flag: ''
                })
            }
        }
    }

    async onDeleteCountry() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete Country',
            message: "Are you sure you wants to delete the record : " + selectedRow.countryName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteCountry()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteCountry() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'countries/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                countryName: '',
                countryStatus: '',
                phoneCode: '',
                flag: '',
                selectedRow: null
            }, () => {
                this.loadContries()
                confirmAlert({
                    title: 'Country',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Country',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    onCheckboxChange = (e) => {
        this.setState({
            [e.target.id]: e.target.checked ? 1 : 0
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleChange = selectedRoleCard => {
        this.setState(
            { selectedRoleCard },
            () => console.log(`Option selected:`, this.state.selectedRoleCard)
        );
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            description: editorState && draftToMarkdown(convertToRaw(editorState.getCurrentContent())),
        })
    }


    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditCountry(type)
        })
    }

    render() {
        const { countryList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay active={isLoading} spinner >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteCountry}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right mx-2"
                                            onClick={this.onAddEditCountry.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right"
                                            onClick={this.onAddEditCountry.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearState()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="countryName">Country Name </label>
                                        <input type="text" onChange={this.onTextChange} id="countryName" value={this.state.countryName} className="form-control" />
                                        {this.validator.message('countryName', this.state.countryName, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="countryStatus">Country Status </label>
                                        <div className="custom-control custom-switch">
                                            <input type="checkbox" className="custom-control-input"
                                                onChange={this.onCheckboxChange} id="countryStatus"
                                                value={this.state.countryStatus}
                                                checked={this.state.countryStatus == 1 ? true : false}
                                            />
                                            <label className="custom-control-label" for="countryStatus"></label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="phoneCode">Phone Code </label>
                                        <input type="text" onChange={this.onTextChange} id="phoneCode" value={this.state.phoneCode} className="form-control" />
                                        {this.validator.message('phoneCode', this.state.phoneCode, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="flag">Flag </label>
                                        <input type="text" onChange={this.onTextChange} id="flag" value={this.state.flag} className="form-control" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-info float-right mx-1"
                                onClick={this.state.actionType == 'Update Country' ? this.onEditCountry : this.onCreateCountry}
                            >{'Submit'}</button>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearState()
                                    })
                                }}
                            >{'Cancel'}</button>
                        </div>
                    </section>
                </div>
            </LoadingOverlay>
        )
    }
}

const mapStateToProps = state => ({
    tabSettings: state.userReducer.tabSettings,
    countryList: state.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Countries)
