import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class JobLog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            vendorList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'JobLog Id',
                },
                {
                    field: 'jobLogDateTime',
                    headerName: 'JobLog DateTime',
                },
                {
                    field: 'jobLogMessage',
                    headerName: 'JobLog Message',
                },
                {
                    field: 'jobName',
                    headerName: 'Job',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorJobLog: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            jobLogDateTime: '',
            jobLogMessage: '',
            jobId: null,
            jobMasterList: [],

            countryList: [],
            selectedJobId: null,
        }
        this.onCreateJobLog = this.onCreateJobLog.bind(this)
        this.onAddEditJobLog = this.onAddEditJobLog.bind(this)
        this.onDeleteJobLog = this.onDeleteJobLog.bind(this)
        this.onEditJobLog = this.onEditJobLog.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadJobLog()
        this.loadJobMaster()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadJobLog() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'joblog')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {

                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadJobMaster() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'jobmaster')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    jobMasterList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateJobLog() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            jobLogDateTime,
            jobLogMessage,
            jobId,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'joblog')
            .send({
                jobLogDateTime,
                jobLogMessage,
                jobId,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                jobLogDateTime: '',
                jobLogMessage: '',
                jobId: null,
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadJobLog()
                confirmAlert({
                    title: 'JobLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'JobLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditJobLog() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            jobLogDateTime,
            jobLogMessage,
            jobId,
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'joblog/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                jobLogDateTime,
                jobLogMessage,
                jobId,
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadJobLog()
                confirmAlert({
                    title: 'JobLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'JobLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearJobLog = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditJobLog(type) {
        const { selectedRow, jobMasterList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {

                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update JobLog',
                    id: selectedRow.id,
                    jobLogDateTime: selectedRow.jobLogDateTime,
                    jobLogMessage: selectedRow.jobLogMessage,
                    jobId: selectedRow.jobId,
                    selectedJobId: { id: selectedRow.jobId, jobName: jobMasterList.find(x => x.id === selectedRow.jobId).jobName }

                }, () => {
                    console.log(this.state.selectedJobId)

                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add JobLog',
                    id: null,
                    jobLogDateTime: '',
                    jobLogMessage: '',
                    jobId: null,
                })
            }
        }
    }

    async onDeleteJobLog() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete JobLog',
            message: "Are you sure you wants to delete the record : " + selectedRow.jobLogDateTime + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteJobLog()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteJobLog() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'joblog/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                jobLogDateTime: '',
                jobLogMessage: '',
                jobId: null,
                selectedRow: null
            }, () => {
                this.loadJobLog()
                confirmAlert({
                    title: 'JobLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'JobLog',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    onEditorJobLogChange = (editorJobLog) => {
        this.setState({
            editorJobLog,
            description: editorJobLog && draftToMarkdown(convertToRaw(editorJobLog.getCurrentContent())),
        })
    }

    onChangeJobMaster = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedJobId: newValue,
                jobId: newValue.id
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditJobLog(type)
        })
    }

    render() {
        const { jobMasterList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteJobLog}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditJobLog.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditJobLog.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearJobLog()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}

                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobLogDateTime">jobLogDateTime </label>
                                        <input type="text" onChange={this.onTextChange} id="jobLogDateTime" value={this.state.jobLogDateTime} className="form-control" />
                                        {this.validator.message('jobLogDateTime', this.state.jobLogDateTime, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobId">Job Id </label>
                                        <Select
                                            // className="form-control"
                                            id={"jobId"}
                                            value={this.state.selectedJobId}
                                            onChange={this.onChangeJobMaster}
                                            options={jobMasterList}
                                            getOptionLabel={(option) => option.jobName}
                                            getOptionValue={(option) => option.id}
                                        />
                                        {this.validator.message('selectedJobId', this.state.selectedJobId, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">

                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="jobLogMessage">JobLog Message </label>
                                        <textarea onChange={this.onTextChange} id="jobLogMessage" value={this.state.jobLogMessage} className="form-control" />
                                    </div>
                                </div>
                            </div>
                            <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                <button
                                    type="button"
                                    className="btn btn-secondary float-right mx-1"
                                    onClick={() => {
                                        this.setState({ isPopupOpen: false }, () => {
                                            this.clearJobLog()
                                        })
                                    }}
                                >{'Cancel'}</button>
                                <button
                                    type="button"
                                    className="btn btn-info float-right  mx-1"
                                    onClick={this.state.actionType == 'Update JobLog' ? this.onEditJobLog : this.onCreateJobLog}
                                >{'Submit'}</button>
                            </div>
                        </div>
                    </section>
                </div>
            </LoadingOverlay >
        )
    }
}

const mapJobLogToProps = vendor => ({
    tabSettings: vendor.userReducer.tabSettings,
    vendorList: vendor.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapJobLogToProps,
    mapDispatchToProps
)(JobLog)
