import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';
import SimpleReactValidator from 'simple-react-validator';

class RoleEntity extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            entityList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Entity Id',
                },
                {
                    field: 'entityName',
                    headerName: 'Entity Name',
                },
                {
                    field: 'entityDescription',
                    headerName: 'Entity Description',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true,
                filter: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorEntity: null,
            userEntityList: [],
            selectedUserEntity: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            entityName: "",
            entityStatus: "",
            countryId: null,
            selectedCountry: null,
            countryList: []

        }
        this.onCreateEntity = this.onCreateEntity.bind(this)
        this.onAddEditEntity = this.onAddEditEntity.bind(this)
        this.onDeleteEntity = this.onDeleteEntity.bind(this)
        this.onEditEntity = this.onEditEntity.bind(this)

        this.validator = new SimpleReactValidator({ autoForceUpdate: this })

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadRoleEntity()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadRoleEntity() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'roleentities')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    entityList: response.ResponsePacket,
                    rowData: response.ResponsePacket,
                    isLoading: false
                }, () => {
                    console.log(this.state.entityList)
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateEntity() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            entityName,
            entityDescription,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'roleentities')
            .send({
                entityName,
                entityDescription,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                entityName: '',
                entityDescription: '',
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadRoleEntity()
                confirmAlert({
                    title: 'Entity',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Entity',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditEntity() {
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            return
        }
        const {
            selectedRow,
            id,
            entityName,
            entityDescription,
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'roleentities/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                entityName,
                entityDescription
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadRoleEntity()
                confirmAlert({
                    title: 'Entity',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Entity',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearEntity = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
        this.validator.hideMessages()
    }

    async onAddEditEntity(type) {
        const { selectedRow } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update Entity',
                    id: selectedRow.id,
                    entityName: selectedRow.entityName,
                    entityDescription: selectedRow.entityDescription
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add Entity',
                    id: null,
                    entityName: '',
                    entityDescription: ''
                })
            }
        }
    }

    async onDeleteEntity() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete Entity',
            message: "Are you sure you wants to delete the record : " + selectedRow.entityName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteEntity()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteEntity() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'roleentities/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                entityName: '',
                entityDescription: '',
                selectedRow: null
            }, () => {
                this.loadRoleEntity()
                confirmAlert({
                    title: 'Entity',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Entity',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorEntityChange = (editorEntity) => {
        this.setState({
            editorEntity,
            description: editorEntity && draftToMarkdown(convertToRaw(editorEntity.getCurrentContent())),
        })
    }

    onCountryChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedCountry: newValue,
            })
        }
    }


    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    async onRowDoubleClicked(type) {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        }, () => {
            this.onAddEditEntity(type)
        })
    }

    render() {
        const { entityList, isLoading, isPopupOpen } = this.state
        this.validator.purgeFields();
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ width: '100%', display: isPopupOpen ? 'none' : 'block' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right"
                                            onClick={this.onDeleteEntity}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-info float-right  mx-2"
                                            onClick={this.onAddEditEntity.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-info float-right "
                                            onClick={this.onAddEditEntity.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div style={{ width: '100%', display: !isPopupOpen ? 'none' : 'block' }}>
                                        <strong className="text-center">{this.state.actionType} </strong>
                                        <button
                                            type="button"
                                            className="btn btn-secondary float-right mx-1"
                                            onClick={() => {
                                                this.setState({ isPopupOpen: false }, () => {
                                                    this.clearEntity()
                                                })
                                            }}
                                        >{'Back'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0" style={{ display: isPopupOpen ? 'none' : 'block' }}>
                                        <div style={{ width: '100%', height: '550px', marginTop: '10px' }}>
                                            <div
                                                id="EntityNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                    onRowDoubleClicked={() => this.onRowDoubleClicked('edit')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="content" style={{ display: !isPopupOpen ? 'none' : 'block' }}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="entityName">Entity Name </label>
                                        <input type="text" onChange={this.onTextChange} id="entityName" value={this.state.entityName} className="form-control" />
                                        {this.validator.message('entityName', this.state.entityName, 'required', { className: 'text-danger small-font14' })}
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label style={{ fontWeight: 200 }} htmlFor="entityDescription">Entity Description </label>
                                        <textarea onChange={this.onTextChange} id="entityDescription" value={this.state.entityDescription} className="form-control" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                            <button
                                type="button"
                                className="btn btn-secondary float-right mx-1"
                                onClick={() => {
                                    this.setState({ isPopupOpen: false }, () => {
                                        this.clearEntity()
                                    })
                                }}
                            >{'Cancel'}</button>
                            <button
                                type="button"
                                className="btn btn-info float-right  mx-1"
                                onClick={this.state.actionType == 'Update Entity' ? this.onEditEntity : this.onCreateEntity}
                            >{'Submit'}</button>
                        </div>
                    </section>
                </div>
            </LoadingOverlay>
        )
    }
}

const mapEntityToProps = entity => ({
    tabSettings: entity.userReducer.tabSettings,
    entityList: entity.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapEntityToProps,
    mapDispatchToProps
)(RoleEntity)
