import React from 'react';
import { Provider } from "react-redux";

//Styling
import '../App.css';
import '../assets/css/bootstrap.min.css'
import '../assets/css/style.css'

import Login from '../adminPanel/Login'
import AdminContainer from './AdminContainer'

import configureStore from '../../src/store/configureStore'
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom'

const store = configureStore()

function App() {
    return (
        <Provider store={store}>
            <Router>
                {/* <Rote exact pattern='/game' path="/game" component={GameContainer} /> */}
                <Route
                    exact
                    path="/"
                    render={() => {
                        return (

                            <Redirect to="/login" />
                        )
                    }}
                />
                {/* <Route exact pattern='/' path="/login" component={Login} /> */}
                <Route exact pattern='/login' path="/login" component={Login} />
                <Route exact pattern='/admin' path="/admin" component={AdminContainer} />
            </Router>
        </Provider>
    )
}

export default App;
